<template>
  <v-app :theme="currentTheme">
    <v-app-bar color="primary" dark app>
      <v-toolbar-title>Vue + Vuetify</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-switch v-model="isDark" :label="`Dark Mode: ${isDark ? 'On' : 'Off'}`"></v-switch>
    </v-app-bar>

    <v-main>
      <v-container class="text-center">
        <v-img :src="require('@/assets/logo.png')" class="my-5" contain height="200"></v-img>
        <v-btn color="primary" @click="showDialog = true" :style="sayHiButtonStyle">Say Hi!</v-btn>
        <v-dialog v-model="showDialog">
          <v-card>
            <v-card-title class="text-h5">Greetings</v-card-title>
            <v-card-text>Hi!</v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="green darken-1" text @click="showDialog = false">Okay</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-btn color="error" @click="moveNoHiButton">No Hi</v-btn>
      </v-container>
    </v-main>
  </v-app>
</template>


<script>
export default {
  name: 'App',
  data() {
    return {
      showDialog: false,
      sayHiButtonSize: 100,
      noHiButtonPosition: { top: '50%', left: '50%' },
      isDark: false,
    };
  },
  computed: {
    sayHiButtonStyle() {
      return {
        width: this.sayHiButtonSize + 'px',
        height: this.sayHiButtonSize + 'px',
      };
    },
    noHiButtonStyle() {
      return {
        position: 'absolute',
        top: this.noHiButtonPosition.top,
        left: this.noHiButtonPosition.left,
      };
    },
    currentTheme() {
      return this.isDark ? 'dark' : 'light';
    },
  },
  methods: {
    moveNoHiButton() {
      const x = Math.random() * (window.innerWidth - 100);
      const y = Math.random() * (window.innerHeight - 50);
      this.noHiButtonPosition = { top: `${y}px`, left: `${x}px` };
      this.sayHiButtonSize += 10;
    },
  },
};
</script>

<style scoped>
.v-btn {
  margin: 10px;
}
</style>
